.tips{
    position: absolute;
    width: 100%;
    height: 35vh;
    z-index: 2;
    margin: 0 auto;
    /* background-color: rgba(255, 255, 0, 0.452); */
  }
  .scores{
    width: 60%;
    /* position: relative; */
    margin: auto;
    height: 15vh;
    /* background-color: red; */
  }
  li:nth-child(1){
      color: gold;
  }
  li:nth-child(2){
      color: silver;
  }
  li:nth-child(3){
      color: #CD7F32;
  }
  .inputName{
      display: flex;
      flex-wrap: nowrap;
      width:60% ;
      position: relative;
      left: 20%;
    }
input{
    margin: auto;
    width:90% ;
    top:32vh;
    left: 25%;
    border: none;
    background-color: white;
    height: 4vh;
    border-radius: 5px;
}
.inputName button{
    width: 40%;
    height: 5vh;
    cursor: pointer;
    background-color: green;
    border-radius: 5px;
}
.inputName button:hover{
    background-color: chartreuse;
    color: rgb(221, 53, 2);
}