.App {
  text-align: center;
  box-sizing: border-box;
  width: 40%;
  height: 100vh;
  background-color: gray;
  margin:  auto;
  position: relative;

    
}

.down{
  opacity:1 !important;
  border: 10px solid white;

}
 
.score{
  z-index: 1;
  position: absolute;
bottom: 0;
text-align: center;
width: 100%;
font-size: 30px;
}
  
  @media(max-width:500px){
    .App{
      width: 50vh;
    }

  }