.play{
    width: 150px;
    height: 150px;
    border-radius: 50%;
    padding: 5px;
    margin:auto;
    cursor: pointer;
    background-color: green;
    border: none;
    position: absolute;
    z-index: 1;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    }
    .play:hover{
      background-color: rgb(56, 228, 56);
      color: white;
      border-radius: 50%;
    }
    .play:active{
      border: 5px solid white;
      border-radius: 50%;
    }
    @media(max-width:500px){
 
        .play{
          margin: auto;
        }
    
      }